<template>
  <div id="todolist">
      <header>
          <div class="logo">KToDoList</div>
          <input type="text" placeholder="What needs to be done?" v-model="todo" @keydown.enter="addToDo">
      </header>
      <section>

          <!-- Active列表 -->
          <ul class="active">
              <li>
                  <h2 class="title">Active<span class="number">{{activeList.length}}</span></h2>
                  <i class="arr" @click="arrClick" :class="{rotate: rotateA}" id="arrA"></i>
              </li>
              <li class="todolist" :class="{pullDown: rotateA}" v-for="(item, index) in activeList" :key="'active'+index">
                  <i class="checkbox" @click="checkDone(item.id)"></i>
                  <span class="content">{{item.content}}</span>
                  <i class="trash" @click="delClick(item.id)"></i>
              </li>
          </ul>

          <!-- Completed列表 -->
          <ul class="completed">
              <li>
                  <h2 class="title">Completed<span class="number">{{completedList.length}}</span></h2>
                  <i class="arr" @click="arrClick" :class="{rotate: rotateC}" id="arrC"></i>
              </li>
              <li class="todolist" :class="{pullDown: rotateC}" v-for="(item, index) in completedList" :key="'completed'+index">
                  <i class="checkbox" @click="checkDone(item.id)"></i>
                  <span class="content">{{item.content}}</span>
                  <i class="trash" @click="delClick(item.id)"></i>
              </li>
          </ul>
      </section>
      <footer>
          <button type="text" @click="clearAll">Clear All</button>
      </footer>
  </div>
</template>

<script>
export default {
    name: "todolist",
    data() {
        return {
            todo: "",
            todolist: [],
            rotateA: false,
            rotateC: false
        }
    },
    methods: {
        // 将数据保存到本地
        saveToDoList() {
            localStorage.todolist = JSON.stringify(this.todolist);
        },
        // 监听enter按键的点击事件
        addToDo() {
            /* 如果输入框内的值不为空，则
                1.将新增的 todo添加到 todolist中
                2.更新 todolist本地数据
                3.清空输入框内容
            */
           if(this.todo.trim() !== "") {
               this.todolist.push({
                    id: this.todolist.length,
                    content: this.todo,
                    isDone: false
                })
                this.saveToDoList();
                this.todo = "";
           }
        },
        // 监听 checkDone的点击事件
        checkDone(id) {
            this.todolist[id].isDone = !this.todolist[id].isDone;
            this.saveToDoList();
        },
        // 监听 垃圾桶图标的点击事件
        delClick(id) {
            this.todolist.splice(id, 1)
            this.todolist.forEach(function(ele, index) {
                ele.id = index;
            });
            this.saveToDoList();
        },
        // 监听 底部 clearAll按钮的点击事件
        clearAll() {
            this.todolist = [];
            this.saveToDoList();
            console.log(localStorage.todolist);
        },
        // 监听 箭头 按钮图标的点击事件
        arrClick(e) {
            switch(e.target.id) {
                case "arrA":
                    this.rotateA = !this.rotateA;
                    break;
                case "arrC":
                    this.rotateC = !this.rotateC;
                    break;
            }
        }
    },
    computed: {
        // 利用 todolist的数据筛选出 尚未完成 和 已完成的数据
        activeList: function() {
            let arr = []
            for(let i of this.todolist) {
                if(!i.isDone) {
                    arr.push(i);
                }
            }
            return arr;
        },
        completedList: function() {
            let arr = []
            for(let i of this.todolist) {
                if(i.isDone) {
                    arr.push(i);
                }
            }
            return arr;
        }
    },
    mounted: function() {
        // 从 localStorage中拿出数据保存到 todolist
        this.todolist = localStorage.todolist ? JSON.parse(localStorage.todolist) : [];
    }
}
</script>

<style>
#todolist {
    width: 100%;
}
#todolist header {
    position: relative;
    width: 100%;
    height: 49px;
    background: #dc4c3f;
    border-bottom: 1px solid #d1c6c6;
}
#todolist header .logo {
    position: absolute;
    left: 0;
    height: 49px;
    line-height: 49px;
    margin: 0 20px;
    font-size: 24px;
    color: #fdeff2;
}
#todolist header > input {
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    width: 180px;
    height: 28px;
    font-size: 14px;
    margin: auto 0;
    padding: 0 10px;
    text-indent: 10px;
    border-radius: 5px;
    box-shadow: 0 1px 0 rgba(255,255,255,0.24), 0 1px 6px rgba(0,0,0,0.45) inset;
}


#todolist section {
    width: 100%;
    overflow: hidden;
}
#todolist section > ul > li:first-child {
    position: relative;
}
#todolist section .title {
    position: relative;
    height: 59px;
    line-height: 59px;
    color: #222;
    font-size: 24px;
    text-indent: 16px;
    border-bottom: 1px solid #d1c6c6;
}
#todolist section .number {
    position: absolute;
    top: 0;
    font-size: 14px;
    text-indent: 6px;
    color: #8b8b8b;
}
#todolist section .todolist {
    position: relative;
    height: 46px;
    line-height: 46px;
    margin-left: 16px;
    opacity: 1;
    font-size: 18px;
    border-bottom: 1px solid #d1c6c6;
    transition-property: height opacity;
    transition-duration: .6s;
}
#todolist section .pullDown {
    height: 0;
    opacity: 0;
    transition-property: height opacity;
    transition-duration: .6s;
}
#todolist section .todolist .checkbox {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto 0;
    width: 22px;
    height: 22px;
    border: 1px solid #8b8b8b;
    border-radius: 50%;
}
#todolist section .todolist .content {
    margin-left: 38px;
}
#todolist section .todolist .trash {
    position: absolute;
    top: 0;
    right: 16px;
    bottom: 0;
    margin: auto 0;
    width: 28px;
    height: 28px;
    background: url("~assets/img/todolist_sprite.png") no-repeat;
    background-size: 100%;
}
#todolist section .completed .content {
    color: #a4a4a4;
    text-decoration: line-through;
}
#todolist section .completed .checkbox {
    width: 22px;
    height: 22px;
    background: url("~assets/img/todolist_sprite.png") no-repeat;
    background-size: 100%;
    background-position: 0 -64px;
}
#todolist section .arr {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 16px;
    margin: auto 0;
    width: 28px;
    height: 28px;
    background: url("~assets/img/todolist_sprite.png") no-repeat;
    background-size: 100%;
    background-position: 0 -32px;
    transform: rotate(0deg);
    transition: transform .6s;
}
#todolist section .rotate {
    transform: rotate(180deg);
    transition: transform .6s;
}




#todolist footer {
    position: fixed;
    bottom: 0;
    width: 100%;
}
#todolist footer > button {
    width: 100%;
    height: 60px;
    font-size: 32px;
    border-top: 1px solid #d1c6c6;
    background: #c04236;
}
</style>